import Look from '../../../components/look.vue'
export default {
	components: {
		Look
	},
	data() {
		return {
			title: '提素建功',
			list: [
				{
					pic: '/static/imgs/index/search.png',
					title: '提素建功提素建功提素建功提素建功提素建功提素建功',
					publish: '管理员发布',
					lookNum: '7780'
				},
				{
					pic: '/static/imgs/index/search.png',
					title: '提素建功提素建功提素建功提素建功提素建功提素建功提素建功',
					publish: '管理员发布',
					lookNum: '7780'
				}
			]
		}
	},
	created() {
		this.getData()
	},
	methods: {
		getData() {
			var data = {
				category: "女工天地信息发布", 
				channel: "提素建功"
			}
			this.$axios(this.$api.getList , data , 'post')
			.then(res =>{
				console.log(res, '.then--------');
				if(res.error_code == 0){
					this.list = res.data.data
				}
			})
			.catch(res =>{
				console.log(res,'.catch--------')
			})
		},
	}
}



